.hero{
    display: flex;
    justify-content: space-between;
    position: relative; 
}
.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}
.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);
}

.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.the-best-ad>span{
    z-index: 2;
}

.the-best-ad>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;
}

.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
    color: white;
}

.figures{
    display: flex;
    gap: 2rem;
}

.figures>div{
    display: flex;
    flex-direction: column;
}

.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2){
    color: var(--gray);
    text-transform: uppercase;
}

.hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}

.hero-buttons>:nth-child(1){
    color: white;
    background-color: var(--orange);
    width: 10rem;
}

.hero-buttons>:nth-child(2){
    color: white;
    background-color: transparent;
    width: 10rem;
    border: 2px solid var(--orange);
}

.right-h>.btn{
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
}

.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}

.heart-rate>img{
    width: 2rem;
}

.heart-rate>:nth-child(2){
    color: var(--gray);
}

.heart-rate>:nth-child(3){
    color: white;
    font-size: 1.5rem;
}

.hero-image{
    position: absolute;
    top: 15rem;
    right: 18rem;
    width: 23rem;
}

.hero-image-back{
    position: absolute;
    top: 4rem;
    right: 30rem;
    z-index: -1;
    width: 15rem;
}

.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: 15rem;
    position: absolute;
    top: 40rem;
    right: 40rem;
}
 .calories > img{
    width: 3rem;
}
.calories > div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
} 
.calories>div>:nth-child(1){
    color: var(--gray);
}
.calories>div>:nth-child(2){
    color: white;
    font-size: 1.5rem;
}

.blur-h{
    width: 40rem;
    height: 40rem;
    left: 0;
}

@media screen and (max-width: 1024px) {
    .hero {
        flex-direction: column;
        align-items: center;
        padding: 1rem;
    }

    .left-h, 
    .right-h {
        width: 100%;
        text-align: center;
    }

    .hero-text {
        font-size: 3rem;
        align-items: center;
    }

    .hero-text > div:nth-of-type(3) {
        font-size: 1rem;
        width: 100%;
    }

    .figures {
        justify-content: center;
        gap: 1rem;
    }

    .hero-buttons {
        justify-content: center;
        gap: 1rem;
    }

    .hero-image {
        position: static;
        margin: 2rem auto;
        max-width: 90%;
    }

    .hero-image-back {
        display: none;
    }

    .heart-rate {
        position: static;
        margin-top: 2rem;
    }

    .calories {
        position: static;
        margin-top: 1rem;
    }
    
    /* Remove the 'Join Now' button */
    .right-h > .btn {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .hero-text {
        font-size: 2.5rem;
    }

    .figures > div > span:nth-of-type(1) {
        font-size: 1.5rem;
    }

    .hero-buttons > .btn {
        font-size: 0.8rem;
        padding: 0.5rem 1rem;
    }
}

@media screen and (max-width: 480px) {
    .hero-text {
        font-size: 2rem;
    }

    .hero-text > div:nth-of-type(3) {
        font-size: 0.8rem;
    }

    .figures {
        flex-direction: column;
        gap: 1rem;
        align-items: center;
    }

    .figures > div {
        align-items: center;
    }

    .hero-buttons > .btn {
        width: 100%;
        font-size: 0.7rem;
    }
}

